import Home from './views/common/Home.vue'
import Login from './views/common/Login.vue'
import NotFound from './views/common/404.vue'
import Tip from './views/common/Tip.vue'

import SzPriceBureauProject from './views/sz/SzPriceBureauProject.vue'
import SzNewProject from './views/sz/SzNewProject.vue'
import SzDealData from './views/sz/SzDealData.vue'
import SzNewProjectHouse from './views/sz/SzNewProjectHouse.vue'
import SzReadyProject from './views/sz/SzReadyProject.vue'
import SzReadyProjectHouse from './views/sz/SzReadyProjectHouse.vue'
import SzPriceBureauHouse from './views/sz/SzPriceBureauHouse.vue'
import SzSecondHandList from './views/sz/SzSecondHandList.vue'
import SzSecondHandInfo from './views/sz/SzSecondHandInfo.vue'

import DgNewProject from './views/dg/DgNewProject.vue'
import DgNewProjectHouse from './views/dg/DgNewProjectHouse.vue'


let routes = [
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    {
        path: '/Tip',
        component: Tip,
        name: '',
        hidden: true
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-chrome',//class
        children: [
            { path: '/SzDealData', component: SzDealData, name: ''},
            { path: '/SzNewProject', component: SzNewProject, name: ''},
            { path: '/SzNewProjectHouse', component: SzNewProjectHouse, name: '',hidden:true},
            { path: '/SzReadyProject', component: SzReadyProject, name: ''},
            { path: '/SzReadyProjectHouse', component: SzReadyProjectHouse, name: '',hidden:true},
            { path: '/SzPriceBureauProject', component: SzPriceBureauProject, name: ''},
            { path: '/SzPriceBureauHouse', component: SzPriceBureauHouse, name: '',hidden:true},
            { path: '/SzSecondHandList', component: SzSecondHandList, name: ''},
            { path: '/SzSecondHandInfo', component: SzSecondHandInfo, name: '',hidden:true},



        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-drupal',//class
        children: [
            { path: '/DgNewProject', component: DgNewProject, name: ''},
            { path: '/DgNewProjectHouse', component: DgNewProjectHouse, name: '',hidden:true},

        ]
    },
    {
        path: '*',
        hidden: true,
        redirect: {path: '/404'}
    }
];

export default routes;


